.App {
  text-align: center;
  height: 100vh;
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.conversation {
  flex: 2;
  height: 100vh;
}

.chat_content {
  flex: 8;
  height: 100vh;
  border-left: 0.1px solid #ddd;
  display: flex;
  flex-direction: column;
}

.conversation li {
  text-align: left;
  padding: 15px 0px 15px 10px;
  list-style-type: none;
  border-bottom: 0.1px solid #ddd ;
  font-size: 0.8em;
  font-weight: bold;
  color: #777;
}

.conversation ul {
  padding: 0px;
}

.conversation_selected {
  background-color: cornflowerblue;
  color: white!important;
}

.chat_msg {
  /* background-color: red; */
  flex: 1;
} 

.wrap_msg {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 10px;
}

.avt {
  height: 2em;
  width: 2em;
  border-radius: 1em;
  border: .5px solid #ddd;
  object-fit: cover;
}

.txt_msg {
  margin-top: 2px;
  font-size: 15px;
}

.chat_msg ul{
  padding: 5px 0px 5px 10px;
  text-align: left;
  list-style-type: none;
} 

.name {
  font-size: 10px;
  color: #777;
}

.chat_msg li{
  color: cadetblue;
  font-size: 18px;
  text-align: left;
  display: flex;
} 

.my_msg {
  text-align: right!important;
  padding-right: 10px;
  color: brown!important;
  justify-content: flex-end;
}

.chat_bar {
  height: 40px;
  display: flex;
  align-items: center;
  margin: 10px;
}

.chat_bar input {
  height: 100%;
  flex: 1;
  border: 0.3px solid #dfdfdf;
  border-radius: 10px;
  padding-left: 10px;
}

.chat_bar input {
  height: 100%;
  flex: 1;
  border: 0.3px solid #dfdfdf;
  border-radius: 10px;
  padding-left: 10px;
}

.chat_bar input:focus, input:focus-visible {
  border: 1px solid khaki !important;
  outline: none;
}

.chat_bar button {
  margin-left: 10px;
  min-width: 80px;
  height: 100%;
  background-color: mediumseagreen;
  border: none;
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  border-radius: 5px;
}

